* {
    margin: 0;
    padding: 0;
	min-height:0;
	min-width:0;
}
*, :after, :before {
    box-sizing: border-box;
}
html,
body {
    position: relative;
    height: 100%;
}
body {
    color: #888;
    background: #f1f1f1;
    font-size:15px;
