h1, h2, h3, h4, h5, h6 {
    color: #212121;
    margin: 10px 0;
    font-style: normal;
    font-family: 'Poppins', sans-serif;
}

h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
    line-height: 1.2;
}

h1 { font-size: 48px;}
h2 { font-size: 36px;}
h3 { font-size: 24px;}
h4 { font-size: 18px;}
h5 { font-size: 16px;}
h6 { font-size: 12px;}
h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a { color: #fff;}
p a:hover {color: #1a237e;}
p {
    letter-spacing: normal;
    line-height: 24px;
	color: #96979B;
	font-weight:300;
}
a {
    text-decoration: none;
    color: var(--main-color);
}
a:hover, a:focus, a:active {
    outline: none;
