section {
	padding-top:120px;
	padding-bottom:120px;
	position: relative;
	z-index: 10;
	background:#f5f7fd;
}

.section-heading {
	text-align: center;
    margin: auto;
    float: none;
    padding-bottom: 20px;
}

.section-heading h2 {
	font-weight:700;
  margin-bottom: 25px;
  font-size: 40px;
}

.section-heading h4 {
	color:#777;
	font-size:16px;
  letter-spacing: 5px;
}

.section-heading p{font-size: 22px;line-height: 1.5;}

.center-layout {
  display: table;
  table-layout: fixed;
  height: 100%;
  width: 100%;
  position: relative;
}
.v-align-middle {
  display: table-cell;
  vertical-align: middle;
}

/*=============================================
              		Header
=============================================*/

.navbar-nav {
    float: right;
    margin: 0;
    padding: 0 10px;
}


nav.navbar.bootsnav.navbar-transparent .logo {
	position: relative;
    z-index: 110;
	display: table;
    height: 80px;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
}

.logo {
    position: relative;
    z-index: 110;
    display: table;
    height: 60px;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
}

.logo img {
    max-height: 33px;
	-webkit-transition: all .5s ease;
    transition: all .5s ease;
    display: block;
}

nav .logo > a {
	display: table-cell;
    vertical-align: middle;
}

.navbar-scrollspy .logo img {
	text-align:center;
	margin: 0 auto;
}

nav.bootsnav.navbar-sidebar ul.nav {
	margin: 0 0 30px 0;
}

